import React from 'react';
import { graphql } from 'gatsby';

// Components
import Layout from '../components/Layout/Layout';
import Background from '../components/Background/Background';
import More from '../components/More/More';
import Carousel from '../components/Carousel/Carousel';
import SomePests from '../components/SomePests/SomePests';

export const query = graphql`
	{
		file(relativePath: { eq: "img/back.jpg" }) {
			childImageSharp {
				fluid {
					...GatsbyImageSharpFluid
				}
			}
		}
		allContentfulBlog(limit: 3) {
			edges {
				node {
					title
					slug
					img {
						fluid {
							...GatsbyContentfulFluid
						}
					}
				}
			}
		}
	}
`;
export default function Home({ data }) {
	return (
		<Layout title="صفحه نخست">
			<Background img={data.file.childImageSharp.fluid} />
			<Carousel />
			<div className="pt-5 pb-5">
				<More />
			</div>
			<SomePests pests={data.allContentfulBlog.edges} />
		</Layout>
	);
}
