import React, { Fragment } from 'react';
import Slider from 'react-slick';
import { Link } from 'gatsby';
import classes from './Carousel.module.scss';
import back from '../../img/logo.png';
import Media from 'react-media';
function Carousel() {
	const settings = {
		dots: true,
		slidesToShow: 3,
		slidesToScroll: 1,
		infinite: true,
		speed: 800
	};
	const settingsSmall = {
		dots: true,
		slidesToShow: 1,
		slidesToScroll: 1,
		infinite: true,
		speed: 800
	};
	return (
		<div className="mr-3 ml-3 pt-5 pb-5">
			<Media
				queries={{
					small: '(max-width: 600px)',
					large: '(min-width: 600px)'
				}}
			>
				{(matches) => (
					<Fragment>
						{matches.small && (
							<Slider className="mr-5 ml-5" {...settingsSmall}>
								<Link className={classes.link} to="/ant">
									<div className={classes.cont}>
										<div className="d-flex flex-column p-4">
											<div className="d-flex justify-content-center pb-3">
												<img className={classes.img} src={back} />
											</div>
											<h3 className={classes.name}>محصول یک</h3>
											<div className="d-flex justify-content-center">
												<button className={classes.btn}>مشاهده</button>
											</div>
										</div>
									</div>
								</Link>
								<Link className={classes.link} to="/ant">
									<div className={classes.cont}>
										<div className="d-flex flex-column p-4">
											<div className="d-flex justify-content-center pb-3">
												<img className={classes.img} src={back} />
											</div>
											<h3 className={classes.name}>محصول دو</h3>
											<div className="d-flex justify-content-center">
												<button className={classes.btn}>مشاهده</button>
											</div>
										</div>
									</div>
								</Link>
								<Link className={classes.link} to="/ant">
									<div className={classes.cont}>
										<div className="d-flex flex-column p-4">
											<div className="d-flex justify-content-center pb-3">
												<img className={classes.img} src={back} />
											</div>
											<h3 className={classes.name}>محصول سه</h3>
											<div className="d-flex justify-content-center">
												<button className={classes.btn}>مشاهده</button>
											</div>
										</div>
									</div>
								</Link>
								<Link className={classes.link} to="/ant">
									<div className={classes.cont}>
										<div className="d-flex flex-column p-4">
											<div className="d-flex justify-content-center pb-3">
												<img className={classes.img} src={back} />
											</div>
											<h3 className={classes.name}>محصول چهار</h3>
											<div className="d-flex justify-content-center">
												<button className={classes.btn}>مشاهده</button>
											</div>
										</div>
									</div>
								</Link>
							</Slider>
						)}

						{matches.large && (
							<Slider className="mr-5 ml-5" {...settings}>
								<Link className={classes.link} to="/ant">
									<div className={classes.cont}>
										<div className="d-flex flex-column p-4">
											<div className="d-flex justify-content-center pb-3">
												<img className={classes.img} src={back} />
											</div>
											<h3 className={classes.name}>محصول یک</h3>
											<div className="d-flex justify-content-center">
												<button className={classes.btn}>مشاهده</button>
											</div>
										</div>
									</div>
								</Link>
								<Link className={classes.link} to="/ant">
									<div className={classes.cont}>
										<div className="d-flex flex-column p-4">
											<div className="d-flex justify-content-center pb-3">
												<img className={classes.img} src={back} />
											</div>
											<h3 className={classes.name}>محصول دو</h3>
											<div className="d-flex justify-content-center">
												<button className={classes.btn}>مشاهده</button>
											</div>
										</div>
									</div>
								</Link>
								<Link className={classes.link} to="/ant">
									<div className={classes.cont}>
										<div className="d-flex flex-column p-4">
											<div className="d-flex justify-content-center pb-3">
												<img className={classes.img} src={back} />
											</div>
											<h3 className={classes.name}>محصول سه</h3>
											<div className="d-flex justify-content-center">
												<button className={classes.btn}>مشاهده</button>
											</div>
										</div>
									</div>
								</Link>
								<Link className={classes.link} to="/ant">
									<div className={classes.cont}>
										<div className="d-flex flex-column p-4">
											<div className="d-flex justify-content-center pb-3">
												<img className={classes.img} src={back} />
											</div>
											<h3 className={classes.name}>محصول چهار</h3>
											<div className="d-flex justify-content-center">
												<button className={classes.btn}>مشاهده</button>
											</div>
										</div>
									</div>
								</Link>
							</Slider>
						)}
					</Fragment>
				)}
			</Media>
		</div>
	);
}

export default Carousel;
