import React from 'react';
import { Link } from 'gatsby';
import Img from 'gatsby-image';

function SomePests({ pests }) {
	return (
		<div className="container pt-5 pb-2">
			<h4 className="text-center">انواع آفت ها</h4>
			<hr />
			<div className="row">
				{pests.map((each) => {
					return (
						<div className="col-lg-4 col-md-4 col-sm-12 col-12">
							<Link to={`/${each.node.slug}`}>
								<div className="d-flex flex-column">
									<div className="d-flex justify-content-center">
										<Img
											style={{ width: '150px' }}
											fluid={each.node.img.fluid}
											alt="img"
											className="img-fluid"
										/>
									</div>
									<h5 className="text-center pt-3 pb-3">{each.node.title}</h5>
								</div>
							</Link>
							<hr />
						</div>
					);
				})}
			</div>
		</div>
	);
}

export default SomePests;
